<template>
  <el-dialog
    class="publish-dialog"
    v-model="showDialog"
    :before-close="closeDialog"
  >
    <template #header>
      <header>
        <h2>
          {{ title }}
        </h2>
        <!-- <el-icon><Close /></el-icon> -->
      </header>
    </template>
    <slot name="body"> </slot>
    <template #footer>
      <ElButton @click="$emit('close-dialog')">取消</ElButton>
      <ElButton class="button-confirm" @click="handleConfirm"> 确定</ElButton>
    </template>
  </el-dialog>
</template>

<script setup>
import { ElDialog, ElButton } from "element-plus";
import { ref, watch } from "vue";
const props = defineProps({
  show: {
    default: false,
  },
  title: {
    default: "title",
  },
});
const showDialog = ref(props.show);
const handleConfirm = () => {
  emits("close-dialog");
  emits("confirm-dialog");
};
const emits = defineEmits(["close-dialog", "confirm-dialog"]);
const closeDialog = () => {
  emits("close-dialog");
};
watch(
  () => props.show,
  () => {
    showDialog.value = props.show;
  }
);
</script>

<style lang="less">
.publish-dialog {
  width: 481px;
  .el-dialog__header {
    margin-right: 0px;
    border-bottom: 1px solid lightgray;
    display: flex;
    align-items: center;
    button {
      right: 20px !important;
    }
    .el-dialog__close {
      height: 40px;
      width: 40px;
    }
  }
  .button-confirm {
    color: white;
    background-color: #92d401;
  }
}
</style>
