<template>
  <div class="publish">
    <header>
      <el-form v-model="searchForm">
        <el-form-item label="公告标题:">
          <el-input v-model="searchForm.title" />
        </el-form-item>
        <el-form-item label="日期">
          <el-date-picker
            v-model="searchForm.time"
            type="daterange"
            range-separator="To"
            start-placeholder="Start date"
            end-placeholder="End date"
          />
        </el-form-item>
        <el-form-item label="信息分区">
          <el-input v-model="searchForm.place" />
        </el-form-item>
        <el-button>查询</el-button>
      </el-form>
    </header>
    <div class="action-bar">
      <el-button style="margin-right;: 20px">删除</el-button>
      <el-button @click="showPublishDialog = true">发送私信</el-button>
    </div>
    <el-table :data="tableData" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" />
      <el-table-column property="index" label="序号" width="120" />
      <el-table-column property="title" label="公告标题" width="120" />
      <el-table-column property="title" label="服务商ID" width="120" />
      <el-table-column property="title" label="发布平台" width="120" />
      <el-table-column property="title" label="信息分区" width="120" />
      <el-table-column property="title" label="触发形式" width="120" />
      <el-table-column property="title" label="发送时间" width="120" />
      <el-table-column property="title" label="发布时间" width="120" />
    </el-table>
    <footer>
      <span>共2条</span>
      <div class="left" @click="prePage">
        <el-icon><ArrowLeft /></el-icon>
      </div>
      <span> {{ currentPage }}/{{ totalPage }} </span>
      <div class="rigt" @click="nextPage">
        <el-icon><ArrowRight /></el-icon>
      </div>
    </footer>
  </div>
  <publish-dialog
    :show="showPublishDialog"
    @close-dialog="showPublishDialog = false"
    title="发布私信"
    @confirm-dialog="handleConfirm"
  >
    <template #body>
      <el-form v-model="providerMessage">
        <el-form-item label="发布平台">
          <el-input v-model="providerMessage.platform" />
        </el-form-item>
        <el-form-item label="信息分区">
          <el-select v-model="providerMessage.area" placeholder="请输入">
            <el-option
              v-for="item of ['业务通知', '系统通知', '资金通知', '活动政策']"
              :key="item"
              :value="item"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="服务商ID">
          <el-input v-model="providerMessage.serverId" placeholder="userID" />
        </el-form-item>
        <el-form-item label="私信标题">
          <el-input v-model="providerMessage.title" placeholder="userID" />
        </el-form-item>
        <el-form-item label="私信内容">
          <el-input v-model="providerMessage.content" type="textarea" />
        </el-form-item>
        <el-form-item label="触发形式">
          <el-select v-model="providerMessage.type" placeholder="请输入">
            <el-option
              v-for="item of ['普通', '窗口提示']"
              :key="item"
              :value="item"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="发送时间">
          <el-select v-model="providerMessage.sendTime" placeholder="请输入">
            <el-option
              v-for="item of ['及时发送', '延时发送']"
              :key="item"
              :value="item"
            />
          </el-select>
        </el-form-item>
      </el-form>
    </template>
  </publish-dialog>
</template>

<script setup>
import { onMounted, ref, reactive } from "vue";
import { $getProviderInfo } from "@/pages/internal/network/adminTools/info";
import PublishDialog from "./info/PublishDialog.vue";
import {
  ElButton,
  ElTable,
  ElForm,
  ElFormItem,
  ElInput,
  ElTableColumn,
  ElDatePicker,
  ElSelect,
  ElOption,
} from "element-plus";
const tableData = ref([]);
const showPublishDialog = ref(false);
const searchForm = ref({
  platform: "服务商工作台",
  title: "",
  serverId: "",
  place: "",
  time: "",
  content: "",
  type: "",
  sendTime: "",
});
const selections = ref([]);
const handleConfirm = () => {};
const handleSelectionChange = (val) => {
  selections.value = val;
};
const currentPage = ref(1);
const totalPage = ref(10);
// 服务商私信表单
const providerMessage = reactive({
  platform: "",
  area: "",
  identity: "",
  userId: "",
  title: "",
  content: "",
  type: "",
  sendTime: "",
});
const prePage = () => {
  currentPage.value -= 1;
};
const nextPage = () => {
  currentPage.value += 1;
};
const getTableData = async () => {
  const response = await $getProviderInfo({
    is_active: true,
    platform: "10000",
  });
  const { Code, msg } = response;
  // 获取当前登录用户所有工单数据
  if (Code === 200 && msg === "success") {
    tableData.value = response.details.notifygenerals.map((item, index) => {
      return {
        // 序号
        index: index + 1,
        // 公告标题
        title: item.title,
        // 用户ID
        userId: "",
        // 发布平台
        platform: item.platform,
        // 信息分区
        area: item.platform,
        // 触发形式
        type: item.title,
        // 发送时间
        sendTime: item.title,
        // 发布时间
        pubulishTime: item.title,
      };
    });
  }
};
onMounted(async () => {
  getTableData();
});
</script>

